  margin: 60px;
}
.Co_head {
  text-align: center;
  color: #f7c02d;
}
.Map {
  border-radius: 30px;
}
.Co-elements {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-right: 200px;
  color: #fff;
}
.Co_title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 250px;
}
