.Nav {
  position: fixed;
  top: 0;
  padding: 20px;
  width: 100%;
  height: 30px;
  z-index: 1;
  transition-timing-function: ease-in;
  transition: all 0.5s;
  color: #fff;
}

.Nav_contents {
  display: flex;
  justify-content: space-between;
}
.Nav_yellow {
  background-color: #040404;
}
.Nav_logo {
  position: fixed;
  top: -110px;
  left: 450px;
  width: 450px;

  object-fit: contain;
  padding: 20px;
  cursor: pointer;
}
.Nav_pages {
  cursor: pointer;
  position: fixed;
  right: 90px;
  width: 350px;
  height: 30px;
  font-weight: 800;
  display: flex;
  justify-content: space-between;
  align-content: space-between;
  color: #fff;
}
.Nav_icons {
  position: fixed;
  display: flex;
  align-content: space-between;
  justify-content: space-between;
  top: 5px;
  width: 140px;
  left: 0;
  font-weight: 800;
  object-fit: contain;
  padding: 20px;
  cursor: pointer;
}
.smoothscroll {
  text-decoration: none;
  color: #fff;
}
@media only screen and (max-width: 768px) {
  .Nav {
    position: fixed;
    top: 0;
    padding: 10px;
    width: 100%;
    height: 6%;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
    color: #fff;
  }
  .Nav_logo {
    position: fixed;
    top: 0px;
    right: 40px;
    width: 50%;

    padding: 5%;
    cursor: pointer;
  }
  .Nav_contents {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  .Nav_pages {
    cursor: pointer;
    position: fixed;
    right: 35%;

    width: 10%;
    height: 30px;
    font-weight: 800;
    display: flex;
    justify-content: space-between;
    align-content: space-between;
    color: #fff;
    font-size: 0.6rem;
    gap: 7px;
  }
  .Nav_icons {
    position: fixed;
    display: flex;
    align-content: space-between;
    justify-content: space-between;
    top: 3px;
    width: 80px;
    left: 0;
    font-weight: 800;
    object-fit: contain;
    gap: 0.4rem;
    cursor: pointer;
  }
}
