.Banner {
  position: relative;
  height: 448px;
  color: white;
  object-fit: contain;
}
.Banner_contents {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}
.Banner_title {
  font-size: 2rem;
  font-weight: 700;
  padding-bottom: 0.3rem;
  color: #000;
}

.banner--fadebottom {
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
}
.Banner_button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding-bottom: 0.5rem;
}
.Banner_button:hover {
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.2s;
}
@media only screen and (max-width: 768px) {
  .Banner {
    position: relative;
    height: 10%;
    color: white;
    object-fit: contain;
  }
  .Banner_contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 10%;
  }
  .Banner_title {
    font-size: 1.3rem;
    font-weight: 700;
    padding-bottom: 0.3rem;
    color: #fff;
    margin: 3%;
  }
  .Banner_button {
    height: 0.5%;
    border-radius: 5px;
    background-color: #f7c02d;
    color: #040404;
  }
  .banner--fadebottom {
    height: 1.4rem;
    background-image: linear-gradient(
      180deg,
      transparent,
      rgba(37, 37, 37, 0.61),
      #111
    );
  }
}
