.Ab_contents {
  display: flex;
  flex-direction: row;
  margin: 85px;
  justify-content: space-between;
  color: #fff;
}
.Ab_text {
  margin-right: 10px;
}

.Ab_pic {
  border-radius: 25px;
}
.Ab_desc {
  width: 800px;
  justify-content: center;
}
.Ab_title {
  text-align: center;
  color: #f7c02d;
}
.Ab_img {
  margin-top: 25px;
}
#aboutus {
  text-decoration: none;
}

@media only screen and (max-width: 768px) {
  .Ab_contents {
    display: flex;
    flex-direction: row;
    margin: 2px;
    justify-content: space-between;
    color: #fff;
  }
  .Ab_text {
    text-align: center;
  }

  .Ab_pic {
    border-radius: 25px;
  }
  .Ab_desc {
    width: 200px;
    float: right;
  }
  .Ab_title {
    text-align: center;
  }
  .Ab_pic {
    width: 150px;
    height: 200px;
  }
}
